const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
}

const center = {
  x:canvas.width/2,
  y:canvas.height/2
}

let angle = 0;

// Event Listeners
addEventListener('mousemove', (event) => {
  mouse.x = event.clientX - canvas.width/2
  mouse.y = event.clientY - canvas.height/2

  angle = Math.atan2(mouse.y,mouse.x)

})

addEventListener('resize', () => {
  canvas.width = innerWidth
  canvas.height = innerHeight

  init()
})

// Objects
class Particle {
  constructor(x, y, radius, color, distFromCenter) {
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
    this.distFromCenter = distFromCenter
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.closePath()
  }

  update() {
    this.draw()
    this.x = center.x  + this.distFromCenter * Math.cos(angle)
    this.y = center.y  +  this.distFromCenter * Math.sin(angle)
  }
}

// Implementation
let particles
function init() {
  particles = []

  const particleCount = 200
  const hueIncrement = 360/particleCount

  for (let i = 0; i < particleCount; i++) {
    const x = center.x  + this.distFromCenter * Math.cos(this.distFromCenter)
    const y = center.y  +  this.distFromCenter * Math.sin(this.distFromCenter)
    const hue = 30
    particles.push(new Particle(x, y, 5,`hsl(${hueIncrement*i},100%,50%)`,i))
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate)
  c.fillStyle = 'rgba(0,0,0, 0.2)'
  c.fillRect(0, 0, canvas.width, canvas.height)

  particles.forEach(particle => {
    particle.update()
   })
}

init()
animate()
